.modal_body {
  display: flex;
  font-family: IBM Plex Sans;
}

.modal_alert {
  font-size: 0.8em;
  text-align: center;
}

.modal_body_div{
  padding: 0 1em 1em 1em;
}

.modal_body_left{
  width: 50%;
}

.modal_cancel {
  position: absolute;
  top: -2em;
  right: -1em;
  cursor: pointer;
}

.modal_auth_title {
  font-weight: 700;
  margin-bottom: 1em;
}



.modal_body .form-control:focus{
  box-shadow: none;
}

.modal_body .form-control {
  border-radius: 0;
  font-size: 0.8em;
  font-weight: 500;
  padding: 1em;
  background: #F7F8FA;
}


.modal_right_text.text_top{
  font-size: 0.8em;
  text-align: right;
  font-weight: 400;
  color: #3D3D3D;
}
.text_bottom{
  font-size: 0.7em;
}


.auth_switch {
  color: #2f6ce5;
  font-weight: 600;
  cursor: pointer;
}

.no_border_bottom {
  border-bottom: none;
}
.no_border_left {
  border-left: none;
}
.no_border_right {
  border-right: none;
}
.no_border_top {
  border-top: none;
}
.no_padding_right {
  padding-right: 0;
}
.no_padding_left {
  padding-left: 0;
}

.modal_button {
  margin-top: 1em;
  width: 100%;
  font-size: 0.8em;
  padding: 0.7em;
}

.modal_button.main{
  border-radius: 30px;
  font-weight: 600;
  background: #2F6CE5;
}

.modal_button.sec{
  border: 1px solid #D9D9DB;
}

.modal_wrapper .modal-content{
  position: relative;
  border-radius: 10px;
}

.modal_wrapper .modal-dialog{
  max-width: 900px;
}

.button_group{
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.button_group_switch{
  font-size: 0.9em;
  font-weight: 500;
  color: #495057;
  text-decoration: underline;
  cursor: pointer;
}

.forgot_password{
  text-align: center;
  font-weight: 500;
  cursor: pointer;
}

@media(min-width: 958px){
  .hide_on_mobile{
    display: none;
  }
}

@media(max-width: 958px){

  .modal_wrapper .modal-dialog{
    align-items: flex-end;
    min-height: 100%;
    margin: 0;
  }
  
  .modal_wrapper .modal-content{
    border-radius: 8px 8px 0 0;
    height: 35em;
  }

  .modal_body_right{
    display: none;
  }
  .modal_body{
    justify-content: center;
  }
  .modal_body_left{
    width: 100%;
    max-width: 30em;
  }
  .modal_alert{
    display: none;
  }
  .modal_cancel{
    top: 1.7em;
    right: 2em;
  }
  .modal_cancel svg path{
    fill: #212529;
  }
  .modal_auth_title{
    margin-top: 0.5em;
  }

  .modal_button.main{
    width: 15em;
  }

  .text_bottom{
    font-size: 0.8em;
    text-align: center;
    margin-top: 1.5em;
  }
}

